<template>
	<div class="card-container">

		<post-smtp-card
			type="all"
			:is-pro="postSmtpPro"
			class="post-smtp-card-box"
		>
			<template v-slot:image>
				<post-smtp-img v-if="! postSmtpPro" :src="plugin_dir_url + 'assets/images/card-email.svg'" alt="Email"></post-smtp-img>

				<div v-else>
					<svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
						<path d="M23 18V6C23 4.89333 22.1334 4 21.06 4H2.94004C1.86655 4 1 4.89333 1 6V18C1 19.1067 1.86655 20 2.94004 20H21.06C22.1334 20 23 19.1067 23 18ZM21.3057 5.85333C21.7325 6.29333 21.4997 6.74667 21.2669 6.97333L16.0159 11.9333L21.06 17.3467C21.2152 17.5333 21.3186 17.8267 21.1376 18.0267C20.9694 18.24 20.5814 18.2267 20.4133 18.0933L14.7613 13.12L11.9935 15.72L9.23868 13.12L3.58671 18.0933C3.41858 18.2267 3.03057 18.24 2.86243 18.0267C2.68136 17.8267 2.78483 17.5333 2.94004 17.3467L7.98413 11.9333L2.7331 6.97333C2.50029 6.74667 2.26749 6.29333 2.6943 5.85333C3.1211 5.41333 3.56085 5.62667 3.92299 5.94667L11.9935 12.6667L20.077 5.94667C20.4392 5.62667 20.8789 5.41333 21.3057 5.85333Z" fill="#6291D9"/>
					</svg>
				</div>
			</template>

			<template v-slot:count>
				<div v-if="postSmtpConfigured">
					{{ totalEmails }}
				</div>

				<div v-else>
					0
				</div>
			</template>

			<template v-slot:content>
				<div>
					{{ i18n.__( 'Total Emails', 'post-smtp' ) }}
					<br>
					<span
						class="post-smtp-content-lighter"
						v-if="postSmtpConfigured"
					>{{ title }}</span>

					<span
						class="post-smtp-content-lighter"
						v-else
					>-</span>
				</div>
			</template>
		</post-smtp-card>

		<post-smtp-card
			type="success"
			:is-pro="postSmtpPro"
			class="post-smtp-card-box"
		>
			<template v-slot:image>
				<img v-if="! postSmtpPro" :src="plugin_dir_url + 'assets/images/card-checkbox.svg'" alt="Success">

				<div v-else>
					<svg width="42" height="42" viewBox="0 0 42 42" fill="none" xmlns="http://www.w3.org/2000/svg">
						<circle cx="21" cy="21" r="21" fill="white"/>
						<path d="M19.8162 29H17.2368L11 21.206L13.5794 19.0601L18.5265 23.1803L28.4206 13L31 14.6137L19.8162 29Z" fill="#74B27B"/>
					</svg>
				</div>
			</template>

			<template v-slot:count>
				<div
					v-if="postSmtpConfigured"
				>
					{{ successEmails }}
				</div>

				<div
					v-else
				>0</div>
			</template>

			<template v-slot:content>
				<div>
					{{ i18n.__( 'Successful emails', 'post-smtp' ) }}
					<br>
					<span
						class="post-smtp-content-lighter"
						v-if="postSmtpConfigured"
					>{{ title }}</span>

					<span
						class="post-smtp-content-lighter"
						v-else
					>-</span>
				</div>
			</template>
		</post-smtp-card>

		<post-smtp-card
			type="failed"
			:is-pro="postSmtpPro"
			class="post-smtp-card-box"
		>
			<template v-slot:image>
				<post-smtp-img v-if="! postSmtpPro" :src="plugin_dir_url + 'assets/images/card-warning.svg'" alt="Failed"></post-smtp-img>

				<div v-else>
					<svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
						<path d="M21.6402 17.0073L14.3127 4.32997C14.2015 4.13392 14.0707 3.96402 13.9267 3.81372C12.7426 2.57866 10.6097 2.74857 9.69382 4.32997L2.36637 17.0073C2.11122 17.4386 2 17.8895 2 18.3338C2 19.0331 2.28132 19.6996 2.75237 20.1897C3.22342 20.6863 3.89728 21 4.67583 21H19.3307C20.8878 21 22 19.7257 22 18.3273C22 17.8895 21.8888 17.4386 21.6402 17.0073ZM10.9434 8.91733C10.9434 8.42723 11.3425 8.02207 11.8397 8.02207H12.4351C12.9257 8.02207 13.3314 8.42069 13.3314 8.91733V13.2498C13.3314 13.7399 12.9323 14.1451 12.4351 14.1451H11.8397C11.349 14.1451 10.9434 13.7465 10.9434 13.2498V8.91733ZM12.1407 18.4319C11.2705 18.4319 10.5705 17.7261 10.5705 16.8635C10.5705 15.9944 11.2771 15.2952 12.1407 15.2952C13.0043 15.2952 13.7108 16.001 13.7108 16.8635C13.7108 17.7326 13.0043 18.4319 12.1407 18.4319Z" fill="#E67373"/>
					</svg>
				</div>
			</template>

			<template v-slot:count>
				<div
					v-if="postSmtpConfigured"
				>
					{{ failedEmails }}
				</div>

				<div
					v-else
				>0</div>
			</template>

			<template v-slot:content>
				<div>
					{{ i18n.__( 'Failed emails', 'post-smtp' ) }}
					<br>
					<span
						class="post-smtp-content-lighter"
						v-if="postSmtpConfigured"
					>{{ title }}</span>

					<span
						class="post-smtp-content-lighter"
						v-else
					>-</span>
				</div>
			</template>
		</post-smtp-card>

		<!--	v-if=! isPro	-->
		<post-smtp-card
			type="opened--pro"
			v-if="! postSmtpPro"
			class="pro-remove-margin"
		>
			<template v-slot:image>
				<post-smtp-img :src="plugin_dir_url + 'assets/images/card-opened-email.svg'" alt="Opened"></post-smtp-img>
			</template>

			<template v-slot:count>
				<div style="margin-bottom: 5px;" class="pro-content-header">
					{{ i18n.__( 'Opened emails', 'post-smtp' ) }}
				</div>
			</template>

			<template v-slot:content>
				<post-smtp-img
					style="margin-right: 4px;margin-bottom: -2px;"
					:src="plugin_dir_url + 'assets/images/pro-lock.svg'"
					alt="Unlock"
				></post-smtp-img>
				<a href="https://postmansmtp.com/pricing/?utm_source=dashboard&utm_medium=pro_grid&utm_campaign=plugin">
					<span class="pro-content">
						{{ i18n.__( 'Unlock with pro', 'post-smtp' ) }}
					</span>
				</a>
			</template>
		</post-smtp-card>
		<!--	v-endif	-->

		<post-smtp-card
			type="opened--pro"
			:is-pro="postSmtpPro"
			v-if="postSmtpPro"
			class="post-smtp-card-box"
		>
			<template v-slot:image>
				<post-smtp-img v-if="! postSmtpPro" :src="plugin_dir_url + 'assets/images/card-opened-email.svg'" alt="Opened"></post-smtp-img>

				<div v-else>
					<svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
						<path d="M4 8.00001L12 13L20 8.00001L12 3.00001L4 8.00001ZM22 8.00001V18C22 18.5304 21.7893 19.0392 21.4142 19.4142C21.0391 19.7893 20.5304 20 20 20H4C3.46957 20 2.96086 19.7893 2.58579 19.4142C2.21071 19.0392 2 18.5304 2 18V8.00001C2 7.27001 2.39 6.64001 2.97 6.29001L12 0.640015L21.03 6.29001C21.61 6.64001 22 7.27001 22 8.00001Z" fill="#F9AA39"/>
					</svg>
				</div>
			</template>

			<template v-slot:count>
				<div
					v-if="postSmtpConfigured"
				>
					{{ openedEmails }}
				</div>

				<div
					v-else
				>0</div>
			</template>

			<template v-slot:content>
				<div>
					{{ i18n.__( 'Opened Emails', 'post-smtp' ) }}
					<br>
					<span
						class="post-smtp-content-lighter"
						v-if="postSmtpConfigured"
					>{{ title }}</span>

					<span
						class="post-smtp-content-lighter"
						v-else
					>-</span>
				</div>
			</template>
		</post-smtp-card>

	</div>
</template>

<script>
export default {
	name: 'PostSmtpCardsContainer',
	inject: [ 'i18n' ],

	props: {
		period: {
			type: String,
			default: () => 'month',
		},

		postSmtpConfigured: {
			type: Boolean,
			default: false,
		},

		postSmtpPro: {
			type: Boolean,
			default: false,
		},
	},

	data: ( vm ) => ( {
		plugin_dir_url: vm.$parent.plugin_dir_url,
		admin_url: vm.$parent.admin_url,

		totalEmails: '0',
		successEmails: '0',
		failedEmails: '0',
		title: '',

		openedEmails: '0',

	} ),

	watch: {
		period( newValue ) {
			this.getLogsCounts( newValue );
		}
	},

	methods: {
		getTitle( period ) {
			switch ( period ) {
				case 'day':
					return this.i18n.__( 'Today', 'post-smtp' );

				case 'week':
					return this.i18n.__( 'This week', 'post-smtp' );

				case 'month':
					return this.i18n.__( 'This month', 'post-smtp' );
			}
		},

		getLogsCounts( period ) {
			axios.get( 'email-count', {
				params: {
					period: period,
				}
			} )
				.then( response => {
					this.totalEmails   = response.data.count.total;
					this.successEmails = response.data.count.success;
					this.failedEmails  = response.data.count.failed;

					if ( this.postSmtpPro ) {
						this.openedEmails = response.data.count.opened;
					}

					this.title = this.getTitle( period );
				} );
		}
	},


	mounted() {
		this.getLogsCounts( this.period );
	}
}
</script>

<style scoped>
.card-container * {
	padding: 0;
	margin: 0;
}

.card-container::after {
	content: "";
	display: table;
	clear: both;
}

.post-smtp-content-lighter {
	color: #231F20;
	font-family: Poppins;
	font-size: 10px;
	font-style: normal;
	font-weight: 500;
	line-height: 13px;
}

.pro-content-header {
	color: #5A3F17;
	text-align: center;
	font-family: Poppins;
	font-size: 14px;
	font-style: normal;
	font-weight: 600;
	line-height: 14px; /* 100% */
}

.pro-content {
	color: #5A3F17;
	text-align: center;
	font-family: Poppins;
	font-size: 11px;
	font-style: normal;
	font-weight: 500;
	line-height: 13px; /* 118.182% */
	text-decoration-line: underline;
}

.post-smtp-card-box[is-pro="true"] .post-smtp-content-lighter {
	color: #fff;
}
</style>